import React from "react";
import {NavLink, NavLinkRenderProps, Outlet} from "react-router-dom";
import {Menu} from "antd";
import {routes} from "./routes";

export const StandardFrame = () => {
    const getActiveStyle = (props: NavLinkRenderProps) => {
        const isActive = props.isActive;
        return {
            color: isActive ? "cornflowerblue" : "unset",
            fontWeight: isActive ? "bold" : "normal",
            marginLeft: 10,
            marginRight: 10
        }
    }

    return (
        <div className="App" style={{
            backgroundColor: "#282c34",
            color: "white",
        }}>

            <Menu mode="horizontal">
                <Menu.Item key="home">
                    <NavLink to={routes.home.link()} style={getActiveStyle}>Home</NavLink>
                </Menu.Item>
                <Menu.Item key="test">
                    <NavLink to={routes.test.link()} style={getActiveStyle}>Test page</NavLink>
                </Menu.Item>
                <Menu.Item key="example">
                    <NavLink to={routes.example.link()} style={getActiveStyle}>Example page</NavLink>
                </Menu.Item>
            </Menu>
            <Outlet/>
        </div>
    );
}